/* selectors */
export const getIsAuth= ({user}) => user.isAuth;
export const getEmail = ({ user }) => user.email;

/* action name creator */
const reducerName = 'user';
const createActionName = name => `app/${reducerName}/${name}`;

/* action types */
const AUTHENTICATE = createActionName('AUTHENTICATE');


export const authenticateUser = payload => ({ payload, type: AUTHENTICATE  });


export const reducer = (statePart = {}, action = {}) => {
  switch(action.type) {
    case AUTHENTICATE: {
      return {
        isAuth: true,
        email: action.payload,
      };
    }
    default: return statePart;
  }
};
