import { useState } from 'react';

const useForm = (post) => {
  const [title, setTitle] = useState(post ? post.title : '');
  const [content, setContent] = useState(post ? post.content : '');
  const [price, setPrice] = useState(post ? post.price : 0);
  const [imageUrl, setImage] = useState(post ? post.imageUrl : '');
  const [phone, setPhone] = useState(post ? post.phone : '');
  const [localization, setLocalization] = useState(post ? post.localization : '');
  const [status, setStatus] = useState(post ? post.status : 'published');

  return {
    fields: {
      title,
      content,
      price,
      imageUrl,
      phone,
      localization,
      status,
    },
    methods: {
      setTitle,
      setContent,
      setPrice,
      setImage,
      setPhone,
      setLocalization,
      setStatus,
    },
  };
};

export default useForm;


