import React, { useEffect, useContext } from 'react';
import PropTypes from 'prop-types';
import Loader from '../../common/Loader/Loader';
import ErrorComponent from '../../common/ErrorComponent/ErrorComponent';
import PostForm from '../../features/PostForm/PostForm';
import editOnePost from '../../../utils/editOnePost';

import { connect } from 'react-redux';
import { fetchOnePost,
  getCurrentPost,
  isRequestActive,
  isRequestError } from '../../../redux/currentPostRedux';
import AuthenticationContext from '../../../contexts/AuthenticationContext';

import styles from './PostEdit.module.scss';

const Component = ({ match , fetchOnePost, isLoading, isError, post }) => {
  const postId = match.params.id;
  useEffect(() => {
    fetchOnePost(postId);
  }, [postId, fetchOnePost]);

  const { userEmail } = useContext(AuthenticationContext);

  return (
    <div className={styles.root}>
      <h2>Edit post #{postId}</h2>
      {
        isLoading && <Loader />
      }
      {
        (!!post && post._id === postId) &&
          <PostForm
            post={post}
            submitButton='Edit post'
            userEmail={userEmail}
            submitAction={editOnePost}
            editingId={postId}
            redirectUrl={`/post/${postId}`}
          />
      }
      <ErrorComponent
        isOpen={isError}
        title={'Error - fetching data'}
        message='Can not fetching post data! Please try again later.'
        isLinkToHP={true}
      />
    </div>
  );
};

Component.propTypes = {
  match: PropTypes.object,
  isLoading: PropTypes.bool,
  isError: PropTypes.bool,
  post: PropTypes.object,
  fetchOnePost: PropTypes.func,
};

const mapStateToProps = state => ({
  post: getCurrentPost(state),
  isLoading: isRequestActive(state),
  isError: isRequestError(state),
});

const mapDispatchToProps = (dispatch) => ({
  fetchOnePost: (postId) => dispatch(fetchOnePost(postId)),
});

const Container = connect(mapStateToProps, mapDispatchToProps)(Component);

export {
  Container as PostEdit,
};
