import _v from 'validator';

const validator = {
  isTitle: (value) => value.length > 9,
  isContent: (value) => value.length > 19,
  isEmail: (value) => _v.isEmail(value),
  isUrl: (value) => _v.isURL(value),
  isPhone: (value) => _v.isMobilePhone(value, 'pl-PL'),
  isValidPrice: (value) => {
    if(value >= 0 && ((value * 100 % 1) === 0)) return true;
    return false;
  },
  isLocalization: (value) => value.length > 0,
};

export default validator;
