import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import InputLabel from '@material-ui/core/InputLabel';
import Button from '@material-ui/core/Button';
import Loader from '../../common/Loader/Loader';
import ErrorComponent from '../../common/ErrorComponent/ErrorComponent';
import useForm from './useForm';
import useValidation from './useValidation';

import PropTypes from 'prop-types';

import clsx from 'clsx';

import styles from './PostForm.module.scss';

const PostForm = ({ post, submitAction, userEmail, submitButton, redirectUrl, editingId }) => {
  const {
    fields,
    methods,
  } = useForm(post);

  const {
    fieldsErrors,
    errorsMethods,
    isValidPrice,
  } = useValidation();

  const [isSent, setIsSent] = useState(false);
  const [isSending, setIsSending] = useState(false);
  const [isError, setIsError] = useState(false);

  return (
    <>
      { isSending && <Loader />}
      <form
        className={styles.root}
        onSubmit={(e) => {
          e.preventDefault();
          const isFormValid = errorsMethods.checkAllFields(fields.title, fields.content, fields.imageUrl, fields.phone, fields.localization);
          if(isFormValid) {
            const postId = editingId || null;
            setIsSending(true);
            submitAction({ ...fields }, userEmail, postId)
              .then(result => {
                setIsSending(false);
                setIsSent(true);
              })
              .catch(err => {
                setIsSending(false);
                setIsError(true);
              });
          }
        }}
      >
        <div className={styles.formControl}>
          <TextField
            error={fieldsErrors.titleErr}
            className={styles.inputBig}
            label='title'
            value={fields.title}
            onChange={(e) => {
              methods.setTitle(e.target.value);
              if(fieldsErrors.titleErr) {
                errorsMethods.setTitleError(false);
              }
            }}
            helperText='minimum 10 characters'
          />
        </div>
        <div className={styles.formControl}>
          <TextField
            error={fieldsErrors.imageUrlErr}
            className={styles.inputBig}
            label="Image url"
            value={fields.imageUrl}
            onChange={(e) => {
              methods.setImage(e.target.value);
              if(fieldsErrors.imageUrlErr) {
                errorsMethods.setImageError(false);
              }
            }}
          />
        </div>
        <div className={styles.formControl}>
          <TextField
            error={fieldsErrors.phoneErr}
            label="Phone number"
            value={fields.phone}
            onChange={(e) => {
              methods.setPhone(e.target.value);
              if(fieldsErrors.phoneErr) {
                errorsMethods.setPhoneError(false);
              }
            }}
          />
        </div>
        <div className={styles.formControl}>
          <TextField
            error={fieldsErrors.localizationErr}
            label="Localization"
            value={fields.localization}
            onChange={(e) => {
              methods.setLocalization(e.target.value);
              if(fieldsErrors.localizationErr) {
                errorsMethods.setLocalizationError(false);
              }
            }}
          />
        </div>
        <div className={clsx(styles.formControl, styles.smallControls)}>
          <FormControl variant="outlined" className={styles.status}>
            <InputLabel htmlFor="status-select">
              Age
            </InputLabel>
            <Select
              native
              value={fields.status}
              onChange={(e) => methods.setStatus(e.target.value)}
              labelWidth={60}
              inputProps={{
                name: 'status',
                id: 'status-select'
              }}
            >
              <option value={'published'}>aktualny</option>
              <option value={'draft'}>szkic</option>
              <option value={'closed'}>nieaktualny</option>
            </Select>
          </FormControl>
          <TextField
            className={styles.price}
            type="number"
            label="Price"
            variant="outlined"
            value={fields.price}
            InputProps={{
              endAdornment: <InputAdornment position="end">zł</InputAdornment>,
            }}
            onChange={(e) => {
              if(isValidPrice(e.target.value)) {
                methods.setPrice(e.target.value);
              }
            }}
          />
        </div>
        <div className={styles.formControl}>
          <TextareaAutosize
            value={fields.content}
            className={clsx(styles.content, fieldsErrors.contentErr ? styles.error : null)}
            placeholder='Content - minimum 20 characters'
            onChange={(e) => {
              methods.setContent(e.target.value);
              if(fieldsErrors.contentErr) {
                errorsMethods.setContentError(false);
              }
            }}
          />
        </div>
        <div className={styles.btnControl}>
          <Button
            variant="contained"
            color="secondary"
            type="submit"
          >{submitButton ? submitButton : 'Submit'}
          </Button>
        </div>
        {
          isSent && <Redirect to={redirectUrl} />
        }
      </form>
      <ErrorComponent
        isOpen={isError}
        title={'Error - sending data'}
        message='Can not add post! Please try again later.'
        isLinkToHP={true}
      />
    </>
  );
};

PostForm.propTypes = {
  post: PropTypes.object,
  submitAction: PropTypes.func,
  userEmail: PropTypes.string,
  redirectUrl: PropTypes.string,
  submitButton: PropTypes.string,
  editingId: PropTypes.string,
};

export default PostForm;
