import { useState } from 'react';
import validator from '../../../utils/validator';

const useValidation = () => {
  const [titleErr, setTitleError] = useState(false);
  const [contentErr, setContentError] = useState(false);
  const [imageUrlErr, setImageError] = useState(false);
  const [phoneErr, setPhoneError] = useState(false);
  const [localizationErr, setLocalizationError] = useState(false);

  const checkAllFields = (titleValue, contentValue, imageUrlValue, phoneValue, localizationValue) => {
    const isTitleValid = validator.isTitle(titleValue);
    const isContentValid = validator.isContent(contentValue);
    const isUrlValid = validator.isUrl(imageUrlValue);
    const isPhoneValid = validator.isPhone(phoneValue);
    const isLocalizationValid = validator.isLocalization(localizationValue);
    setTitleError(!isTitleValid);
    setContentError(!isContentValid);
    setImageError(!isUrlValid);
    setPhoneError(!isPhoneValid);
    setLocalizationError(!isLocalizationValid);
    return (isTitleValid && isContentValid && isUrlValid && isPhoneValid && isLocalizationValid);
  };

  const isValidPrice = validator.isValidPrice;

  return {
    fieldsErrors: {
      titleErr,
      contentErr,
      imageUrlErr,
      phoneErr,
      localizationErr,
    },
    errorsMethods: {
      checkAllFields,
      setTitleError,
      setContentError,
      setImageError,
      setPhoneError,
      setLocalizationError,
    },
    isValidPrice,
  };

};

export default useValidation;
