import axios from 'axios';
import api from '../config/api';

/* selectors */
export const getCurrentPost = ({currentPost}) => currentPost.data;
export const isRequestActive = ({ currentPost }) => currentPost.request.active;
export const isRequestError = ({ currentPost }) => currentPost.request.error;

/* action name creator */
const reducerName = 'current-post';
const createActionName = name => `app/${reducerName}/${name}`;

/* action types */
const FETCH_START = createActionName('FETCH_START');
const FETCH_SUCCESS = createActionName('FETCH_SUCCESS');
const FETCH_ERROR = createActionName('FETCH_ERROR');

/* action creators */
export const fetchStarted = payload => ({ payload, type: FETCH_START });
export const fetchSuccess = payload => ({ payload, type: FETCH_SUCCESS });
export const fetchError = payload => ({ payload, type: FETCH_ERROR });

export const fetchOnePost= (postId) => {
  return (dispatch) => {
    dispatch(fetchStarted());
    const url = `${api.url}/${api.endPoints.posts}/${postId}`;
    axios.get(url)
      .then(result => dispatch(fetchSuccess(result.data.post)))
      .catch(err => dispatch(fetchError()));
  };
};


/* reducer */
export const reducer = (statePart = [], action = {}) => {
  switch (action.type) {
    case FETCH_START: {
      return {
        ...statePart,
        request: {
          active: true,
          error: false,
        },
      };
    }
    case FETCH_SUCCESS: {
      return {
        ...statePart,
        request: {
          active: false,
          error: false,
        },
        data: action.payload,
      };
    }
    case FETCH_ERROR: {
      return {
        ...statePart,
        request: {
          active: false,
          error: true,
        },
      };
    }
    default:
      return statePart;
  }
};
