import React, { useEffect, useContext } from 'react';
import PropTypes from 'prop-types';
import Loader from '../../common/Loader/Loader';
import ErrorComponent from '../../common/ErrorComponent/ErrorComponent';
import PostInfo from '../../common/PostInfo/PostInfo';
import { Link } from 'react-router-dom';

import { connect } from 'react-redux';
import { fetchOnePost,
  getCurrentPost,
  isRequestActive,
  isRequestError } from '../../../redux/currentPostRedux';
import AuthenticationContext from '../../../contexts/AuthenticationContext';

import styles from './Post.module.scss';

const Component = ({match, fetchOnePost, post, isLoading, isError }) => {
  const postId = match.params.id;
  useEffect(() => {
    fetchOnePost(postId);
  }, [fetchOnePost, postId]);

  const { isAuth, userEmail } = useContext(AuthenticationContext);

  return (
    <div className={styles.root}>
      {
        isLoading && <Loader />
      }
      {
        (!!post && !isLoading && !isError) &&
        <section>
          {
            (isAuth && post.author === userEmail)
            &&
            <div className={styles.linkWrapper}>
              <Link className={styles.link} to={`/post/${post._id}/edit`}>
                Edit
              </Link>
            </div>
          }
          <PostInfo {...post}/>
        </section>
      }
      <ErrorComponent
        isOpen={isError}
        title={'Error - fetching data'}
        message='Can not fetching post data! Please try again later.'
        isLinkToHP={true}
      />
    </div>
  );
};

Component.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  post: PropTypes.object,
  match: PropTypes.object,
  fetchOnePost: PropTypes.func,
  isLoading: PropTypes.bool,
  isError: PropTypes.bool,
};

const mapStateToProps = state => ({
  post: getCurrentPost(state),
  isLoading: isRequestActive(state),
  isError: isRequestError(state),
});

const mapDispatchToProps = (dispatch) => ({
  fetchOnePost: (postId) => dispatch(fetchOnePost(postId)),
});


const Container = connect(mapStateToProps, mapDispatchToProps)(Component);



export {
  Container as Post,
};
