import React, { useEffect, useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import ErrorComponent from '../../common/ErrorComponent/ErrorComponent';
import Loader from '../../common/Loader/Loader';
import PostsList from '../../common/PostsList/PostsList';
import Pagination from '@material-ui/lab/Pagination';

import { connect } from 'react-redux';
import { getAll, fetchPosts, isLoadingActive, isLoadingError, getCount } from '../../../redux/postsRedux.js';
import AuthenticationContext from '../../../contexts/AuthenticationContext';
import pagesConfig from '../../../config/pages';

import styles from './Homepage.module.scss';


const Component = ({ fetchPosts, count, posts, isLoading, isError }) => {
  const { isAuth } = useContext(AuthenticationContext);
  const pages = Math.ceil(count / pagesConfig.maxPostsOnPage);
  const [page, setPage] = useState(1);

  useEffect(() => {
    fetchPosts(page);
  }, [fetchPosts, page]);

  return (
    <section className={styles.root}>
      <header className={styles.header}>
        <h2>Posts:</h2>
        {
          isAuth && <Link to='/post/add' className={styles.link}>Add new</Link>
        }
      </header>
      <div className={styles.list}>
        {
          isLoading ?
            <Loader/>
            :
            <PostsList posts={posts}/>
        }

      </div>
      {
        pages > 1 &&
        <div className={styles.paginationWrapper}>
          <Pagination
            count={pages}
            page={page}
            onChange={(e, value) => setPage(value)}
            color="primary"
            siblingCount={0}
          />
        </div>
      }
      <ErrorComponent
        title="Error - fetching data"
        message="Can not fetching posts list. Try again later!"
        isOpen={isError}
      />
    </section>

  );
};

Component.propTypes = {
  fetchPosts: PropTypes.func,
  posts: PropTypes.array,
  isLoading: PropTypes.bool,
  isError: PropTypes.bool,
  userEmail: PropTypes.string,
  count: PropTypes.number,
};

const mapStateToProps = state => ({
  posts: getAll(state),
  isLoading: isLoadingActive(state),
  isError: isLoadingError(state),
  count: getCount(state),
});

const mapDispatchToProps = dispatch => ({
  fetchPosts: (page) => dispatch(fetchPosts(page)),
});

const Container = connect(mapStateToProps, mapDispatchToProps)(Component);

export {
  Container as Homepage,
  Component as HomepageComponent,
};
