import React from 'react';
import Modal from '@material-ui/core/Modal';
import PropTypes from 'prop-types';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';

import styles from './ErrorComponent.module.scss';

const ErrorComponent = ({ title, message, isOpen, isLinkToHP }) => {
  return (
    <div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={isOpen}
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={isOpen}>
          <Paper className={styles.paper}>
            <h3 id="transition-modal-title">{title}</h3>
            <p id="transition-modal-description">{message}</p>
            {
              isLinkToHP && <Link href="/">Back to home page</Link>
            }
          </Paper>
        </Fade>
      </Modal>
    </div>

  );
};


ErrorComponent.propTypes = {
  title: PropTypes.string,
  message: PropTypes.string,
  isOpen: PropTypes.bool,
  isLinkToHP: PropTypes.bool,
};


export default ErrorComponent;
