import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import ErrorComponent from '../../common/ErrorComponent/ErrorComponent';
import Loader from '../../common/Loader/Loader';
import PostsList from '../../common/PostsList/PostsList';
import Pagination from '@material-ui/lab/Pagination';

import { connect } from 'react-redux';
import { getAll, fetchMyPosts, isLoadingActive, isLoadingError, getCount } from '../../../redux/postsRedux.js';
import pagesConfig from '../../../config/pages';

import styles from './MyPosts.module.scss';

const Component = ({ posts, count, fetchMyPosts, isLoading, isError }) => {
  const pages = Math.ceil(count / pagesConfig.maxPostsOnPage);
  const [page, setPage] = useState(1);

  useEffect(() => {
    fetchMyPosts(page);
  }, [fetchMyPosts]);

  return (
    <section className={styles.root}>
      <header className={styles.header}>
        <h2>My posts:</h2>
        <Link to='/post/add' className={styles.link}>Add new</Link>
      </header>
      <div>
        {
          isLoading ?
            <Loader/>
            :
            <PostsList posts={posts}/>
        }

      </div>
      {
        pages > 1 &&
        <div className={styles.paginationWrapper}>
          <Pagination
            count={pages}
            page={page}
            onChange={(e, value) => setPage(value)}
            color="primary"
            siblingCount={0}
          />
        </div>
      }
      <ErrorComponent
        title="Error - fetching data"
        message="Can not fetching posts list. Try again later!"
        isOpen={isError}
      />
    </section>

  );
};

Component.propTypes = {
  fetchMyPosts: PropTypes.func,
  posts: PropTypes.array,
  isLoading: PropTypes.bool,
  isError: PropTypes.bool,
  count: PropTypes.number,
};

const mapStateToProps = state => ({
  posts: getAll(state),
  isLoading: isLoadingActive(state),
  isError: isLoadingError(state),
  count: getCount(state),
});

const mapDispatchToProps = dispatch => ({
  fetchMyPosts: (page) => dispatch(fetchMyPosts(page)),
});

const Container = connect(mapStateToProps, mapDispatchToProps)(Component);

export {
  Container as MyPosts,
  Component as MyPostsComponent,
};
