import axios from 'axios';
import api from '../config/api';
import { authenticateUser } from './userRedux';
import pagesConfig from '../config/pages';
const maxPostsOnPage = pagesConfig.maxPostsOnPage;

/* selectors */
export const getAll = ({posts}) => posts.data.posts;
export const isLoadingActive = ({ posts }) => posts.loading.active;
export const isLoadingError = ({ posts }) => posts.loading.error;
export const getPost = ({posts}, id) => posts.data.find(post => post.id === id);
export const getCount = ({posts}) => posts.data.count;

/* action name creator */
const reducerName = 'posts';
const createActionName = name => `app/${reducerName}/${name}`;

/* action types */
const FETCH_START = createActionName('FETCH_START');
const FETCH_SUCCESS = createActionName('FETCH_SUCCESS');
const FETCH_ERROR = createActionName('FETCH_ERROR');

/* action creators */
export const fetchStarted = payload => ({ payload, type: FETCH_START });
export const fetchSuccess = payload => ({ payload, type: FETCH_SUCCESS });
export const fetchError = payload => ({ payload, type: FETCH_ERROR });

/* thunk creators */
//demo fetching to see how error, loading and succes are working on frontend
export const fetchPosts = (page) => {
  return (dispatch) => {
    dispatch(fetchStarted());
    const url = `${api.url}/${api.endPoints.posts}?limit=${maxPostsOnPage}&skip=${(page - 1) * maxPostsOnPage}`;
    axios.get(url)
      .then(result => {
        dispatch(fetchSuccess({ posts: result.data.posts, count: result.data.count }));
        if(result.data.user) {
          const email = result.data.user.email;
          dispatch(authenticateUser(email));
        }
      })
      .catch(err => dispatch(fetchError()));
  };
};

export const fetchMyPosts = (page) => {
  return (dispatch) => {
    dispatch(fetchStarted());
    const url = `${api.url}/${api.endPoints.posts}/my?limit=${maxPostsOnPage}&skip=${(page - 1) * maxPostsOnPage}`;
    axios.get(url)
      .then(result => dispatch(fetchSuccess({ posts: result.data.posts, count: result.data.count })))
      .catch(err => dispatch(fetchError()));
  };
};

/* reducer */
export const reducer = (statePart = [], action = {}) => {
  switch (action.type) {
    case FETCH_START: {
      return {
        ...statePart,
        loading: {
          active: true,
          error: false,
        },
      };
    }
    case FETCH_SUCCESS: {
      return {
        ...statePart,
        loading: {
          active: false,
          error: false,
        },
        data: {
          posts: action.payload.posts,
          count: action.payload.count
        },
      };
    }
    case FETCH_ERROR: {
      return {
        ...statePart,
        loading: {
          active: false,
          error: action.payload,
        },
      };
    }
    default:
      return statePart;
  }
};
