import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

import clsx from 'clsx';
import api from '../../../config/api';

import { connect } from 'react-redux';
import { getIsAuth } from '../../../redux/userRedux.js';

import styles from './Navigation.module.scss';

const Component = ({className, isAuth }) => {
  const linksList = (
    <ul className={styles.navigation}>
      <li className={styles.navigationItem}>
        <NavLink exact to='/' className={styles.navigationLink} activeClassName={styles.active}>Home</NavLink>
      </li>
      <li className={styles.navigationItem}>
        <NavLink exact to='/post/add' className={styles.navigationLink} activeClassName={styles.active}>Add</NavLink>
      </li>
      <li className={styles.navigationItem}>
        <NavLink exact to='/myposts' className={styles.navigationLink} activeClassName={styles.active}>My</NavLink>
      </li>
    </ul>
  );


  return (
    <nav className={clsx(className, styles.root)}>
      <div>
        {
          isAuth ?
            linksList
            :
            <div className={styles.navigationItem}>
              <NavLink exact to='/' className={styles.navigationLink} activeClassName={styles.active}>Home</NavLink>
            </div>
        }
      </div>
      <div>
        {
          isAuth ?
            <a href={`${api}/user/logout`} className={styles.navigationLink}>Logout</a>
            :
            <a href={`${api}/auth/google`} className={styles.navigationLink}>Login</a>
        }
      </div>
    </nav>
  );
};

Component.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  isAuth: PropTypes.bool,
};

const mapStateToProps = state => ({
  isAuth: getIsAuth(state),
});

const Container = connect(mapStateToProps)(Component);

export {
  Container as Navigation,
  Component as NavigationComponent,
};
