import React from 'react';
import PropTypes from 'prop-types';

import styles from './PostInfo.module.scss';

const PostInfo = ({ _id, title, content, createdAt, imageUrl, author, status, price, phone, localization  }) => {
  const date = new Date(createdAt).toLocaleDateString();

  return (
    <article className={styles.root}>
      <div className={styles.header}>
        <h2>{title}</h2>
        <div><strong>{localization} {date}</strong></div>
      </div>

      <div className={styles.description}>
        <img className={styles.image} src={imageUrl} alt={title}/>
        <div className={styles.textContent}>
          <div>
            <div className={styles.shortData}>
              <p><span>autor: <strong>{author}</strong></span></p>
              <p><span>tel.: <strong>{phone}</strong></span></p>
              <p><strong>{status === 'closed' ? 'ogłoszenie nieaktualne' : 'ogłoszenie aktualne'}</strong></p>
            </div>
            <p className={styles.content}>{content}</p>
          </div>
          <div>
            <h2 className={styles.price}><span className={styles.priceItem}>{price} zł</span></h2>
          </div>
        </div>
      </div>
    </article>
  );
};

PostInfo.propTypes = {
  _id: PropTypes.string,
  title: PropTypes.string,
  content: PropTypes.string,
  createdAt: PropTypes.string,
  imageUrl: PropTypes.string,
  author: PropTypes.string,
  status: PropTypes.string,
  price: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  phone: PropTypes.string,
  localization: PropTypes.string,
};

export default PostInfo;
