import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { createMuiTheme, StylesProvider, ThemeProvider } from '@material-ui/core/styles';
import { CssBaseline } from '@material-ui/core';

import { MainLayout } from './components/layout/MainLayout/MainLayout';
import { Homepage } from './components/views/Homepage/Homepage';
import { Post } from './components/views/Post/Post';
import { PostEdit } from './components/views/PostEdit/PostEdit';
import { PostAdd } from './components/views/PostAdd/PostAdd';
import { NotFound } from './components/views/NotFound/NotFound';
import { MyPosts } from './components/views/MyPosts/MyPosts';

import { connect } from 'react-redux';
import { getIsAuth, getEmail } from './redux/userRedux';
import AuthenticationContext from './contexts/AuthenticationContext';

import PropTypes from 'prop-types';

const theme = createMuiTheme({
  palette: {
    primary: { main: '#2B4C6F' },
  },
});

const Component = ({isAuth, userEmail }) => (
  <BrowserRouter>
    <StylesProvider injectFirst>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <AuthenticationContext.Provider value={{ isAuth, userEmail }} >
          <MainLayout>
            <Switch>
              <Route exact path='/' component={Homepage} />
              <Route exact path='/post/add' component={isAuth ? PostAdd : NotFound} />
              <Route exact path='/post/:id' component={Post} />
              <Route exact path='/post/:id/edit' component={isAuth ? PostEdit : NotFound} />
              <Route exact path='/myposts'  component={isAuth ? MyPosts : NotFound} />
              <Route path='*' component={NotFound} />
            </Switch>
          </MainLayout>
        </AuthenticationContext.Provider >
      </ThemeProvider>
    </StylesProvider>
  </BrowserRouter>
);

Component.propTypes = {
  isAuth: PropTypes.bool,
  userEmail: PropTypes.string,
};

const mapDispatchToProps = (state) => ({
  isAuth: getIsAuth(state),
  userEmail: getEmail(state),
});

const Container = connect(mapDispatchToProps)(Component);

export {
  Container as App,
};
